.timeline-wrapper {
  margin       : 1em 0;
  grid-column  : 1 / -1;
  border-radius: 5px;
  border       : 5px solid var(--clr-dark);
  overflow     : auto;
}

.timeline {
  position                : relative;
  display                 : grid;
  // grid-template-columns: 7em 1fr;
  // grid-auto-flow: dense;
  grid-auto-rows          : 4em;
}

.notes {
  // overflow: auto;
}

.instrument {
  // grid-column: 1;
  display    : grid;
  place-items: center;

  border          : 1px solid var(--clr-dark);
  background-color: var(--clr-primary);

  color         : white;
  font-weight   : 700;
  text-transform: capitalize;
}

.node {
  cursor: pointer;
  border: 1px solid var(--clr-dark);
}

.active-node {
  background-color: var(--clr-primary)
}

.marker {
  position        : absolute;
  top             : 0;
  bottom          : 0;
  left            : 7em;
  width           : 5px;
  border-radius   : 5px;
  background-color: var(--clr-accent);
  transition      : opacity 300ms linear;
  z-index         : 5;
  box-shadow      : -2px 0 3px #000000aa
}