.slider {
  display        : flex;
  gap            : 0.5em;
  flex-wrap      : wrap;
  align-items    : center;
  justify-content: center;
  position       : relative;
}

.reading,
.name {
  position      : absolute;
  pointer-events: none;
  font-weight   : 700;
  color         : var(--clr-white);
  text-shadow   : 1px 1px 1px #00000040,
    1px -1px 1px #00000040,
    -1px 1px 1px #00000040,
    -1px -1px 1px #00000040;
}

.name {
  left          : 1em;
  top           : 50%;
  transform     : translateY(-50%);
  text-transform: capitalize;
  width         : 7ch;
}

.reading {
  right    : 1em;
  top      : 50%;
  transform: translateY(-50%);
}

.input-slider {
  width             : 100%;
  -webkit-appearance: none;
  border-radius     : 5px;
  height            : 2.5em;
  cursor            : w-resize;

  &::-webkit-slider-thumb {
    -webkit-appearance: none;
    opacity           : 0;
    width             : 0px;
    height            : 0px;
  }

  &::-moz-range-thumb {
    -webkit-appearance: none;
    opacity           : 0;
    width             : 0px;
    height            : 0px;
  }
}

@media(max-width:575px) {
  .input-slider {
    height: 3em;
  }

  .name,
  .reading {
    font-size: 0.9rem;
  }
}